<template>
  <f7-page @page:init="onPageInit" ref="homePage">
    <f7-navbar>
      <f7-nav-title>HN Ã— ðŸŒ€</f7-nav-title>
    </f7-navbar>

    <f7-list media-list class="topic-list">
      <f7-list-item
        v-for="item in listData"
        :link="'/item/' + item.id"
        :key="item.id"
      >
        <div slot="title">
          {{ item.title }}
        </div>
        <div slot="text">{{ item.author }} â€¢ {{ item.time }}</div>
        <span slot="after">
          <f7-chip :text="item.comments + ''" class="chip-container"></f7-chip>
        </span>
      </f7-list-item>
    </f7-list>
  </f7-page>
</template>

<script>
import {
  f7Page,
  f7Navbar,
  f7NavTitle,
  f7List,
  f7ListItem,
  f7Chip
} from "framework7-vue";

export default {
  components: {
    f7Page,
    f7Navbar,
    f7NavTitle,
    f7List,
    f7ListItem,
    f7Chip
  },

  data() {
    return {
      listData: []
    };
  },

  methods: {
    onPageInit() {
      this.getData();
    },

    async getData() {
      const resp = await fetch(`${process.env.VUE_APP_HOST}/api/v1/hn/ask`);
      this.listData = (await resp.json()).data;
    }
  }
};
</script>

